<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="手机号：" required>
          <div>{{form.cellPhone}}</div>
        </el-form-item>
        <el-form-item label="姓名：" required>
          {{form.userName}}
        </el-form-item>
        <el-form-item label="邮箱：" required>
          <div>{{form.email}}</div>
        </el-form-item>
        <el-form-item label="状态：" required>
          <div>{{form.enabled == 0 ? '启用' : '禁用'}}</div>
        </el-form-item>
        <el-form-item label="角色：" required>
          {{form.roleName}}
        </el-form-item>
        <div class="flex-row-center">
          <el-button @click="cancle">返 回</el-button>
        </div>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findById } from '@/api/systemSetting/userManage'
export default {
  data(){
    return{
      form:{
        enabled:'0'
      },
      addLoading:false,
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      findById({id:this.$route.query.id}).then(res=>{
        console.log('用户详情',res)
        if(res.code == 200){
          this.form = res.data
        }else{
          this.$message.error(res.message);
        }
      })
    },
    cancle(){
      this.$router.replace({path:'/systemSetting/userManage'})
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>

</style>